.shop-container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.list {
  height: 100px;
  display: flex;
  justify-content: space-evenly;
  /* padding: 0px 80px; */
  align-items: center;
  position: relative;
  background: #e0efe3;
  padding: 30px 0px;
}

.list::after {
  margin-top: 170px;
  margin-left: 400px;
  content: "";
  width: 0px;
  height: 0px;
  position: absolute;
  border-style: solid;
  border-width: 20px 20px 20px 0px;
  border-color: transparent #e0efe3 transparent transparent;
  display: inline-block;
  vertical-align: top;
  transform: rotate(-90deg);
  margin-right: 5px;
}

.extraItems {
  width: 35%;
  display: flex;
  justify-content: space-between;
}

.items {
  margin: auto;
  width: 90%;
  margin-top: 50px;
  gap: 10px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}

.shop-item-divider::after {
  content: "";
  display: block;
  border-style: solid;
}

.shop-item-img {
  max-height: 320px !important;
  width: 100%;
  border-bottom: 4px solid rgb(46, 46, 46);
  border-radius: 20px;
  box-shadow: 8px 8px 2px rgba(0, 0, 0, 0.26);
  object-fit: contain;
}

.nothing-found-shop-text {
  color: grey;
  display: flex;
  flex-direction: column;
  text-align: center;
}

.shop-search-bar button {
  position: absolute;
  align-items: center;
  margin-left: -40px;
  /* padding: 7px 15px; */
  background-color: transparent;
  border: none;

  border-radius: 7px;
}

.shop-search-bar button:hover {
  background-color: rgba(255, 255, 255, 0.266);
  border: 0.6px solid rgba(0, 0, 0, 0.486);
}

.shop-item-img:hover {
  border-bottom: 4px solid #9b27b0;
  box-shadow: 8px 8px 2px #9b27b05d;
  /* transform: skew(120px); */
}
.extraLineBtn {
  margin: 10px 0px;
  text-align: center;
  position: relative;
}

.shop-search-bar {
  width: max-content;
  gap: 10px;
}

.shop-reset-filters {
  width: 28%;
  height: 40px;
  margin-top: 20px !important;
}

@media all and (max-width: 900px) {
  .list::after {
    margin-left: 220px;
    position: absolute;
    margin-top: 350px;
  }
  .front-font {
    width: 100%;
  }
  .front-font h3 {
    font-size: 2em;
    text-align: center;
    margin: 20px 0px;
  }

  .items {
    margin: auto;
    width: 95%;
    margin-top: 50px;
    gap: 10px;
    display: flex;
    flex-direction: column;
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
  .extraItems {
    margin-top: 30px;
    width: 95%;
    display: flex;
    justify-content: space-around;
    /* gap: 30px; */
    margin-bottom: 30px;
    flex-direction: column;
  }
  .list {
    height: fit-content;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    padding: 0px 5px;
    align-items: center;
    position: relative;
    background: #e0efe3;
  }
  .shop-search-bar {
    width: 85% !important;
    height: auto !important;
    align-items: center;
    margin-left: 5px;
    display: flex;
  }

  .favoriteIconSHop {
    height: 0.8em !important;
    width: 0.8em !important;
  }
  .nothing-found-shop-text {
    font-size: 1.7em !important;
  }
  .shop-search-bar button {
    position: absolute;
    align-items: center;
    margin-left: 70%;
    padding: 7px;
    background-color: transparent;
    border: none;
  }
  .SearchBarfirstChild {
    font-size: 1em !important;
    width: max-content;
  }
  .shop-reset-filters {
    position: absolute;
    width: 100%;
    height: 40px;
  }
}
