.container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  text-align: left;
  /* height: 100vh !important; */
  overflow: hidden !important;
  /* background-color: purple; */
}
.bg {
  margin-top: 40px;
  height: 550px;
  width: 550px;
}

.bg:hover {
  background-color: black;
}

.text {
  width: 40%;
  display: flex;
  flex-direction: column;
}

.text button {
  margin-top: 10px;
  width: 120px;
}

.bottom-design {
  /* display: block; */
  /* height: 50px; */
  /* width: 100%; */
  padding-bottom: -20px;
  bottom: -22px;
  left: 0;
  /* transform: rotateZ(1deg); */
  /* border-top-right-radius: 1000px; */
  /* background-image: linear-gradient(90deg, #000000, rgba(0, 0, 0, 0)); */
  position: absolute;
}

@media all and (max-width: 900px) {
  .container {
    margin-top: 50%;
    flex-direction: column;
    display: flex;
    justify-content: space-around;
    align-items: center;
    text-align: left;
  }
  .bg {
    height: 300px;
    width: 300px;
    padding-left: 10vh;
    display: none;
  }
  .text {
    font-size: 1em;
    width: 90%;
    display: flex;
    flex-direction: column;
  }
  .text h2 {
    font-size: 4em;
  }
} ;
